@font-face {
  font-family: "Open-Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local(""), url("../fonts/open-sans-latin_cyrillic-300.woff2") format("woff2"), url("../fonts/open-sans-latin_cyrillic-300.woff") format("woff");
}
@font-face {
  font-family: "Open-Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("../fonts/open-sans-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/open-sans-latin_cyrillic-regular.woff") format("woff");
}
@font-face {
  font-family: "Open-Sans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local(""), url("../fonts/open-sans-latin_cyrillic-600.woff2") format("woff2"), url("../fonts/open-sans-latin_cyrillic-600.woff") format("woff");
}
@font-face {
  font-family: "Open-Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local(""), url("../fonts/open-sans-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/open-sans-latin_cyrillic-700.woff") format("woff");
}
img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  height: 100vh;
  font-family: "Open-Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 28px;
  font-weight: 300;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
}

.page {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0;
  min-height: 100vh;
  min-width: 320px;
  background-color: white;
  color: black;
}

.page__footer {
  margin-top: auto;
  padding-bottom: 98px;
}
@media (max-width: 1023px) {
  .page__footer {
    padding-bottom: 53px;
  }
}

.logo {
  display: block;
}

.logo svg {
  display: block;
}

.main-nav {
  position: absolute;
  z-index: 10;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  padding-top: 21px;
  width: 880px;
  min-height: 338px;
}
@media (max-width: 1023px) {
  .main-nav {
    display: block;
    padding-top: 0;
    width: 100%;
    min-height: auto;
  }
}

@media (max-width: 1023px) {
  .main-nav--opened,
.main-nav--no-js {
    min-height: 100vh;
    background-color: white;
  }
}

@media (max-width: 1023px) {
  .main-nav--no-js {
    position: static;
    transform: none;
    padding-bottom: 25px;
    min-height: auto;
  }
}

.main-nav__header {
  order: 1;
  margin-top: auto;
}
@media (max-width: 1023px) {
  .main-nav__header {
    display: flex;
    align-items: flex-end;
    padding-left: 21px;
  }
}
@media (max-width: 767px) {
  .main-nav__header {
    padding-left: 17px;
  }
}

@media (max-width: 1023px) {
  .main-nav__logo {
    padding-bottom: 5px;
  }
}

@media (max-width: 1023px) {
  .main-nav__logo--opened,
.main-nav__logo--no-js {
    display: none;
  }
}

.main-nav__toggle {
  display: none;
}
@media (max-width: 1023px) {
  .main-nav__toggle {
    cursor: pointer;
    position: relative;
    display: block;
    margin-left: auto;
    border: none;
    width: 50px;
    height: 50px;
  }
}

@media (max-width: 1023px) {
  .main-nav__toggle--closed {
    background-color: #0ad6c2;
  }
}
.main-nav__toggle--closed:hover, .main-nav__toggle--closed:focus {
  background-color: #09beac;
}
.main-nav__toggle--closed:active {
  background-color: #06756a;
}
@media (max-width: 1023px) {
  .main-nav__toggle--closed::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 22px;
    height: 1px;
    background-color: black;
    border-radius: 1px;
    box-shadow: 0 6px 0 0 black, 0 -6px 0 0 black;
  }
}

.main-nav__toggle--opened {
  background-color: transparent;
}
.main-nav__toggle--opened::before, .main-nav__toggle--opened::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 2px;
  width: 31px;
  height: 1px;
  background-color: rgba(19, 20, 22, 0.2);
}
.main-nav__toggle--opened::before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.main-nav__toggle--opened::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.main-nav__toggle--opened:hover::before, .main-nav__toggle--opened:hover::after, .main-nav__toggle--opened:focus::before, .main-nav__toggle--opened:focus::after {
  background-color: #131416;
}
.main-nav__toggle--opened:active::before, .main-nav__toggle--opened:active::after {
  background-color: rgba(19, 20, 22, 0.5);
}

@media (max-width: 1023px) {
  .main-nav__toggle--no-js {
    display: none;
  }
}

@media (max-width: 1023px) {
  .main-nav__site-nav--closed {
    display: none;
  }
}

@media (max-width: 1023px) {
  .main-nav__site-nav--opened,
.main-nav__site-nav--no-js {
    display: block;
  }
}

.site-nav__list {
  margin: 0 5px 15px 2px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  display: grid;
  grid-template-columns: repeat(4, max-content);
  grid-row-gap: 10px;
  justify-content: space-between;
  list-style: none;
  color: rgba(255, 255, 255, 0.7);
  line-height: 32px;
  letter-spacing: -0.01em;
}
@media (max-width: 1023px) {
  .site-nav__list {
    display: block;
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 0;
    padding: 50px 0 0;
    color: #48494c;
    font-weight: normal;
    font-size: 20px;
    line-height: 28px;
  }
}

@media (max-width: 1023px) {
  .site-nav__item {
    position: relative;
    margin-bottom: 28px;
    padding: 17px;
    background-color: whitesmoke;
  }
}
@media (max-width: 1023px) {
  .site-nav__item::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 20px;
    width: 8px;
    height: 8px;
    border-right: solid 2px rgba(44, 46, 63, 0.5);
    border-bottom: solid 2px rgba(44, 46, 63, 0.5);
    transform: rotate(-45deg) translateY(-50%);
  }
}
@media (max-width: 1023px) {
  .site-nav__item:last-child {
    margin-bottom: 0;
  }
}

.site-nav__item a {
  display: block;
  color: inherit;
}
.site-nav__item a:hover, .site-nav__item a:focus {
  color: white;
}
@media (max-width: 1023px) {
  .site-nav__item a:hover, .site-nav__item a:focus {
    color: black;
    font-weight: 600;
  }
}
.site-nav__item a:active {
  color: rgba(255, 255, 255, 0.5);
}
@media (max-width: 1023px) {
  .site-nav__item a:active {
    font-weight: 600;
    color: rgba(0, 0, 0, 0.3);
  }
}

