    display: none;
  }
}
.type__wrapper--mountain::before {
  left: -19px;
  top: -18px;
}
.type__wrapper--mountain::after {
  right: -23px;
  bottom: -23px;
}

.type__wrapper--kids::before {
  content: "";
  position: absolute;
  z-index: 1;
  top: -32px;
  right: -36px;
  border: 17px solid #0ad6c2;
  border-radius: 50%;
  width: 95px;
  height: 95px;
}
@media (max-width: 1023px) {
